export function getAddressLevel (id1,id2) {
  return request({
    url: `/order/mall/district/getSysDistrict`,
    method: 'post',
    data:{
      level:id1,
      code:id2
    }
  })
}
// 主营列表
export function getShopCategoryList () {
  return request({
    url: `/sys/business/shopInfo/getShopCategoryList`,
    method: 'get',
  })
}
// 创建店铺
export function addShop(data) {
  return request({
    url: `/sys/business/shopInfo/addShop`,
    method: 'post',
    data: data
  })
}