<template>
  <div class="pageContent bgfff">
    <el-card class="filter-container" shadow="never">
      <el-form :model="form" ref="form" label-width="100px" class="demo-ruleForm">
        <el-form-item label="店铺名称：" required>
          <el-input v-model="form.shopName" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="主营商品：" required>
          <el-select v-model="form.categoryCode" placeholder="请选择主营产品">
            <el-option v-for="item in industry" :key="item.id" :label="item.name" :value="item.code"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="省市区：" required>
          <el-col :span="8">
            <el-form-item required>
              <el-select @change="address1" v-model="form.addressProvinceCode" placeholder="请选择省">
                <el-option v-for="(item,index) in addressList1" :key="index" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item required>
              <el-select @change="address2" v-model="form.addressCityCode" placeholder="请选择市">
                <el-option v-for="(item,index) in addressList2" :key="index" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item required>
              <el-select v-model="form.addressCountyCode" placeholder="请选择区">
                <el-option v-for="(item,index) in addressList3" :key="index" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="详细地址：" required>
          <el-input v-model="form.addressDetails" maxlength="50" show-word-limit></el-input>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { 
  getAddressLevel,
  getShopCategoryList,
  addShop
} from '@/api/shop/createShop'
export default {
  data(){
    return{
      form:{},
      industry:[],                  // 创建 - 下拉 - 行业
      addressList1:[],              // 创建 - 下拉 - 地址1级
      addressList2:[],              // 创建 - 下拉 - 地址2级
      addressList3:[],              // 创建 - 下拉 - 地址3级
    }
  },
  created(){
    if(this.$route.query.shopType == 1){
      console.log('微商城')
    }
    if(this.$route.query.shopType == 3){
      console.log('健身房')
    }
  },
  methods:{
    // 创建店铺 - 下拉选择主营
    getShopCategoryList(){
      getShopCategoryList().then(res=>{
        console.log('获取下拉行业',res)
        this.industry = res.data
      })
    },
    // 创建店铺 - 下拉选择地址
    getAddressInput(id1,id2){
      getAddressLevel(id1,id2).then(res=>{
        console.log('获取下拉框地址',res)
        if(id1 == 1){
          this.addressList1 = res.data
          return;
        }
        if(id1 == 2){
          this.addressList2 = res.data
          return;
        }
        if(id1 == 3){
          this.addressList3 = res.data
          return;
        }
      })
    },
    // 创建店铺 - 下拉地址 2级
    address1(val){
      this.addressList2 = [];
      if(this.createForm.addressCityCode) this.createForm.addressCityCode = '';
      this.addressList3 = [];
      if(this.createForm.addressCountyCode) this.createForm.addressCountyCode = '';
      this.getAddressInput(2,val);
    },
    // 创建店铺 - 下拉地址 3级
    address2(val){
      this.addressList3 = [];
      if(this.createForm.addressCountyCode) this.createForm.addressCountyCode = '';
      this.getAddressInput(3,val);
    },
    // input 
    change(e){
      this.$forceUpdate()
    }
  },
  computed: {
    ...mapState([
      'user'
    ])
  },
}
</script>
<style lang="less" scoped>
  .bgfff{
    height: 100%;
  }
</style>